.NavbarItems {
    background: linear-gradient(90deg, rgb(238, 116, 238) 0%,rgb(71, 71, 248) 100%);
    height: 60px;
    display: flex;
    text-decoration: none;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 18px;
}
.navbar-logo {
    margin-bottom: 5px;
    animation: blinkingText 2s infinite;
    color: white;
    justify-self: start;
    margin-left: -30px;
    cursor: pointer;
    text-decoration: none;
    font-size: 35px;
    font-weight: bold;
    font-family: "Sofia", sans-serif;
}
@keyframes blinkingText {
    0%		{ color: #30d438;}
	25%		{ color: #76a1e2;}
	50%		{ color: #5e0e13;}
	75%		{ color: #0ffaee;}
	100%	{ color: #a8d504;}
  }
.fa-film{
    margin-left: 1.7rem;
    font-size: 2.5rem;
}
.nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 1rem;
    margin-top: 10px;
}
.nav-links{
    color: aliceblue;
    text-decoration: none;
    padding: 0.5rem 1rem;
    font-family:  "Trirong", serif;
}
.nav-links-m{
    color: aliceblue;
    text-decoration: none;
    padding: 0.2rem 1rem;
    font-family:  "Trirong", serif;
    background-color: rgb(62, 146, 243);
    border-radius: 6px;
}
.nav-links:hover{
    background-color: #2f39c2;
    border-radius: 6px;
    transition: all 0.2s ease-out;
    text-decoration: none;
    color: azure;
}
.nav-links-m:hover{
    background-color: #2f38be;
    border-radius: 6px;
    transition: all 0.2s ease-out;
    text-decoration: none;
    color: azure;
}
.fa-bars{
    color: aliceblue;
}
.menu-icon{
    display:none;
}
@media screen and (max-width: 960px) {
    .NavbarItems{
        position:relative;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 360px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }
    .nav-menu.active{
        background: #6668f4;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }
    .nav-links{
        text-align: center;
        margin-left: -30px;
        padding: 1.8rem;
        width: 100%;
        display: table;
    }
    .nav-links:hover{
        background-color: #313191;
        border-radius: 0;
    }
    .navbar-logo {
        position: absolute;
        margin-left: 0;
        top: 6px;
        left:0;
        transition: translate(25%,50%);
    }
    .menu-icon{
        display: block;
        position: absolute;
        top:0;
        right: 0;
        transform: translate(-100%,60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
    
    .fa-items{
        color: white;
        font-size: 2rem;
    }
    .nav-links-m{
        display: block;
        text-align: center;
        padding: 15px;
        margin-left: 10% ;
        border-radius: 5px;
        width: 70%;
        text-decoration: none;
        background-color:  rgb(63, 144, 219);
        color: white;
        font-size: 24px;
    }
    .nav-links-m:hover{
        background: #535deb;;
        color: white;
    }
}