.quickSearchContainer{
    margin-left:50px;
    margin-top:20px;
}
.quickSearchHeading{
    text-align: left;
    color:#041231;
    font-size:23px;
    animation: blinker 1s linear infinite;
    font-weight: bold;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
@keyframes blinker {
    50% {
      opacity: 0;
    }
  }
.tileContainer{
    height:228px;
    width:175px;
    display: inline-block;
    margin-top: 28px;
    margin-bottom: 20px;
    margin-right: 3%;
    box-shadow: 0px 2px 12px rgb(37, 37, 37);
}
.tileComponent1{
    width:100%;
    float: left;
}
.componentHeading{
    text-align: center;
    color:#0e1b38;
    font-size:16px;
    font-weight: bold;
}
.componentSubHeading{
    text-align: center;
    color:#424346;
    font-size:12px;
    font-weight: bold;
}
.tileContainer:hover{
    /*transform: rotate(0deg)*/
    transform: scale(1.1,1.1);
    
}
.imageStyle{
    height:180px;
    width:100%
}
.note{
    background-color: #898b92;
}
.help{
    text-align: center;
    text-decoration: none;
    font-size: 22px;
    background-color: #55565a;
}
.icon{
    font-size: 30px;
    color: red;
    text-align: center;
}