:root{
    --primary: #3acbf7;
}
.btn{
    padding: 8px 20px;
    background-color: rgb(94, 224, 241);
    border-radius: 4px;
    margin-left: 70px;
    outline: none;
    border: none;
    cursor: pointer;
}
.btn:hover{
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: whitesmoke;
    color: rgb(34, 10, 56);
    transition: 250ms;
}
.btn--primary{
    background-color: var(--primary);
}
.btn--outline{
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}
.btn--medium{
    padding: 8px 20px;
    border-radius: 4px;
    color: white;
    font-size: 16px;
}
.btn--large{
    padding: 10px 23px;
    border-radius: 4px;
    color: white;
    font-size: 20px;
}